import React,{useState,useContext} from 'react'
import { AuthContext } from '../../context/authContext';
import { Link } from 'react-router-dom';
import { logout } from '../util/logout';

export default function BottomNavBar(){
    const [auth,setAuth]=useContext(AuthContext)
    if(auth.isLoggedIn){
    return(
        <nav className="navbar fixed-bottom navbar-expand-lg navbar-dark navbar-bottom-custom-container"> 
            {auth.isAdmin?<div className="navbar-bottom-custom">
            <div className="navbar-bottom-custom-item">
                <Link to="/userList">
                <i className="fa fa-users"></i>
                <p>UserList</p>
                </Link>
            </div>
            <div className="navbar-bottom-custom-item">
            <Link to="/paymentInfo">
            <i className="far fa-money-bill-alt"></i>
            <p>Payments</p>
            </Link>
            </div>
            <div className="navbar-bottom-custom-item">
                <Link to="/wbjeequestionupload">
                <i className="fa fa-question-circle"></i>
                <p>Wbjee</p>
                </Link>
            </div>
                <div className="navbar-bottom-custom-item">
                <Link to="/jeemainquestionupload">
                <i className="fa fa-question-circle"></i>
                <p>Jeemain</p>
                </Link>
            </div>
            </div>:
            <div className="navbar-bottom-custom">
                          <div className="navbar-bottom-custom-item">
                <Link to="/userprofile">
                <i className="fa fa-users"></i>
                <p>Profile</p>
                </Link>
            </div>
            <div className="navbar-bottom-custom-item" onClick={()=>{logout(setAuth)}}>
            <i className="far fa-money-bill-alt"></i>
            <p>Logout</p>
            </div>
            <div className="navbar-bottom-custom-item">
                <Link to="/videos">
                <i className="fa fa-question-circle"></i>
                <p>videos</p>
                </Link>
            </div>  
            </div>
            }
        </nav>
    );
    }
    return(
        <div></div>
    );
}